module.exports = {
  path: 'ui',
  getChildRoutes(partialNextState, cb) {
    require.ensure([], (require) => {
      cb(null, [
        require('./routes/box'),
        require('./routes/button'),
        require('./routes/card'),
        require('./routes/color'),
        require('./routes/components'),
        require('./routes/feedback'),
        require('./routes/grid'),
        require('./routes/icon'),
        require('./routes/iconBox'),
        require('./routes/navigation'),
        require('./routes/typography'),
      ])
    })
  }
}



