module.exports = {
  path: 'form',
  getChildRoutes(partialNextState, cb) {
    require.ensure([], (require) => {
      cb(null, [
        require('./routes/components'),
        require('./routes/form'),
        require('./routes/layout'),
        require('./routes/steps'),
        require('./routes/validation'),
      ])
    })
  }
}



